/* Basic CSS reset and styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #F7FFF7;
  color: #1A1A2E;
  line-height: 1.6;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

a {
  color: #FF6B6B;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover {
  color: #4ECDC4;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}

section {
  padding: 4rem 0;
}

@media (max-width: 768px) {
  section {
    padding: 3rem 0;
  }
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}
